var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"app "},[_c('div',{staticClass:"text"},[_c('p',[_vm._v("FICHE PEDAGOGIQUE")]),_c('p',[_vm._v("SEBA DE KAMITOLOGIE ")]),_c('p',[_vm._v(" OBTENEZ LE SESHW (MASTER) DE KAMITOLOGIE A VOTRE RYTHME ")])]),_c('p',[_vm._v("Les diplômes de Seshw (Master) et de Seshw-nsw (doctorat) de kamitologie en ligne de l’IKCAD sont les premiers diplômes panafricains libres en langue française. Le programme complet de Seshw/Seshw-nsw est composé de 18 unités d’enseignement appelées sebaw (Seba au singulier) réparties sur 8 semestres. Mais l’IKCAD vous offre la possibilité d’obtenir ces diplômes sur une plus longue période en validant les sebaw selon votre rythme. ")]),_c('div',{staticClass:"title"},[_vm._v(" Définition de la discipline ")]),_c('p',[_vm._v(" La kamitologie est le domaine des études africaines qui se caractérise fondamentalement par une perspective africaine à savoir l'adoption systématique et consciente d'un cadre conceptuel généré par les Africains eux-mêmes. La formation est novatrice et unique au monde car elle anticipe sur la société panafricaine de demain. Elle est conforme aux prescriptions du Manifeste culturel d’Afrocentricity International pour l’éducation du peuple noir (site dyabukam.com). ")]),_c('div',{staticClass:"title"},[_vm._v("Objectif ")]),_c('p',[_vm._v(" Acquérir les connaissances fondamentales africaines. Se doter de solides méthodes de travail et développer ses qualités d’analyse, de synthèse et de rédaction. Développer son sens critique, créatif et son agentivité au service du monde africain. ")]),_c('div',{staticClass:"title"},[_vm._v("Débouchés ")]),_vm._v(" Ils sont celles offertes par les prometteuses perspectives de la renaissance africaine. En effet la montée de la puissance démographique de l’Afrique s’accompagne d’un montée de la conscience positive. Ce phénomène déjà palpable suscitera des demandes dans tous les domaines de l’existence qu’il faut pouvoir. Le titulaire du seshw peu aussi bien poursuivre ses études en Seshw-nsw (doctorat) à l’IKCAD. "),_c('div',{staticClass:"title"},[_vm._v(" Programme ")]),_c('p',[_vm._v(" Le Seshw est composé de 12 sebaw en raison de 3 sebaw par semestre. L’étudiant peut choisir de suivre 1, 2 ou 3 sebaw par semestre dans l’ordre de la progression pédagogique. Voici la liste des sebaw. Pour plus de détail consulter le catalogue de l’IKCAD. ")]),_c('div',{staticClass:"title"},[_vm._v(" Semestre 1 ")]),_c('p',[_vm._v(" kmt 7007 : Théorie et méthodologie de l’afrocentricité. Kmt 7017 : Historiographie diopienne. Kmt 7027 : Initiation aux mdw ntr Ecriture et grammaire 1. Semestre 2 : kmt 7037 Les grands mouvements de la pensée africaine ; kmt 7047 : Préhistoire et histoire ancienne de l’Afrique ; Kmt 7057 : Initiation aux mdw ntr Ecriture et grammaire 2. Semestre 3 : kmt 7067 : Spiritualité et renaissance africaines ; kmt 7077 : Histoire et civilisation de l’époque impériale ; kmt 7087 : Initiation aux mdw ntr : Atelier de lecture et traduction de textes. Semestre 4 : kmt 7097 : Histoire et culture de la diaspora africaine ; kmt 7107 : Les grandes invasions de l’Afrique ; kmt 7117 : Langue Pan-Africaine Moderne : le kishwali. Les cours sont proposés sur un format en ligne strict. ")]),_c('div',{staticClass:"title"},[_vm._v(" Imy r sebayt et l’équipe pédagogique")]),_c('p',[_vm._v("La direction pédagogiques des Sebaw de kamitologie est assurée par le professeur Ama Mazama ; « Imy r Sebayt » (directrice de l’enseignement) de kamitologie à l’IKCAD. Elle est professeure d’Etudes Africaines aux USA. Elle conduit une équipe d’enseignants et de chercheurs africains tous compétents en kamitologie. ")]),_c('div',{staticClass:"title"},[_vm._v(" Tarif de la formation")]),_c('p',[_vm._v("Continent: 175.000 frs cfa (cent soixante quinze mille)")]),_c('p',[_vm._v("Diaspora: 335.00 Euros(€) (Trois cent trente cinq) ")]),_c('div',{staticClass:"title"},[_vm._v(" Informations et inscription")]),_c('p',[_vm._v("Pour toute demande d’informations et/ou d’inscription envoyer une mail au responsable de l’administration : Sesh Traoré Adama : keetmaatabidjan@gmail.com. ")]),_c('div',{staticClass:"footer"},[_vm._v(" Edition du 23 mai 2022")])])}]

